.reviews{
    @include my(50px);
    &__items{
        margin-top: 40px;
       display: flex;
       justify-content: center;
    }
    &__img{
        @include wh(150px ,150px);
        @include mx(0,70px);
        @media (max-width:$tablet-sm - 1){
            @include wh(50px);
            @include mx(0);
        }
        .img-fluid{
            border-radius:100%;
            @include wh(100% ,100%);
        }
    }
    &__descr {
        @media (max-width:$tablet-sm - 1){
            @include mx(20px,0);
        }
    }
    &__subtitle{
        @include fs-lh(26px,30px);
        font-weight: $font-w500;
        color: $color__white;
        @include my(0,25px);
    }
    &__text{
        @include fs-lh(18px,22px);
        width: 300px;
        font-weight: $font-w300;
        color: $color__white;

    }
}