.form {
    &.form--order {
        width: 100%;
    }
    &__item {
        @include my(0,10px);
        @extend %flex-column;
    }
    &-label {
        @include fs-lh(18px,20px);
        font-weight: $font-w300;
        @include my(0,15px);
    }
    &-input {
       border-radius:  6px;
       padding: 6px;
       &::placeholder {
        padding: 6px;
       }
    }
}